/** @jsx jsx */
import { jsx, Box, useColorMode } from 'theme-ui'
import { useConfig } from 'docz'

import { Menu, Sun, Github } from 'gatsby-theme-docz/src/components/Icons'


import { media } from '~theme/breakpoints'

const wrapper = {
  bg: 'header.bg',
  position: 'relative',
  zIndex: 1,
  height: '60px',
  backgroundImage: 'linear-gradient(to right, rgb(146, 254, 157) 0%, rgb(0, 201, 255) 100%)'
}

const innerContainer = {
  px: 4,
  position: 'relative',
  height: "100%",
  display: 'flex',
  justifyContent: 'flex-end',
  alignItems: 'center'
}

const menuIcon = {
  display: 'none',
  position: 'absolute',
  top: 'calc(100% + 15px)',
  left: 30,
  [media.tablet]: {
    display: 'block',
  },
}

export const menuButton = {
  color: 'header.text',
  opacity: 0.5,
  cursor: 'pointer',
}

export const headerButton = {
  display: "flex",
  outline: 'none',
  p: '12px',
  border: 'none',
  borderRadius: 9999,
  bg: 'header.button.bg',
  color: 'header.button.color',
  fontSize: 0,
  fontWeight: 600,
  cursor: 'pointer'
}

export const editButton = {
  position: 'absolute',
  bottom: -40,
  right: 30,
  bg: 'transparent',
  color: 'muted',
  fontSize: 1,
  textDecoration: 'none',
  borderRadius: 'radius',
}


export const Header = props => {
  const { onOpen } = props
  const {
    themeConfig: { showDarkModeSwitch },
  } = useConfig()
  const [colorMode, setColorMode] = useColorMode()

  const toggleColorMode = () => {
    setColorMode(colorMode === 'light' ? 'dark' : 'light')
  }

  return (
    <div sx={wrapper} data-testid="header">
      <Box sx={menuIcon}>
        <button sx={menuButton} onClick={onOpen}>
          <Menu size={25} />
        </button>
      </Box>
      <div sx={innerContainer}>
        <Box sx={{ mr: 2 }}>
          <a
            href="https://github.com/matthewriabinin"
            sx={headerButton}
            target="_blank"
            rel="noopener noreferrer"
          >
            <Github size={15} />
          </a>
        </Box>
        {showDarkModeSwitch && (
          <Box sx={{ mr: 2 }}>
            <button sx={headerButton} onClick={toggleColorMode}>
              <Sun size={15} />
            </button>
          </Box>
        )}
      </div>
    </div>
  )
}
